"use client";

import { useEffect, useState } from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Skeleton } from "@/components/ui/skeleton";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Transaction } from "./payment-form"; // Import the type
import { Button } from "./ui/button";
import { RefreshCw } from "lucide-react";

// Define the props
interface TransactionHistoryProps {
  initialTransactions: Transaction[];
  onRefresh: (apiTransactions: Transaction[]) => void;
}

export function TransactionHistory({ initialTransactions, onRefresh }: TransactionHistoryProps) {
  // Use the initial transactions from props
  const [transactions, setTransactions] = useState(initialTransactions);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchHistory = async () => {
    try {
      setLoading(true);
      setError(null);
      const res = await fetch("/api/history");
      const data = await res.json();

      if (!res.ok) {
        throw new Error(data.error || "Failed to fetch history");
      }
      
      // Call the onRefresh prop to update the parent state
      onRefresh(data);

    } catch (err) {
      setError(err instanceof Error ? err.message : "An unknown error occurred");
    } finally {
      setLoading(false);
    }
  };

  // Fetch history on initial load
  useEffect(() => {
    fetchHistory();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  
  // Update local state when the prop changes
  useEffect(() => {
    setTransactions(initialTransactions);
  }, [initialTransactions]);

  const formatCurrency = (amountInKobo: number) => {
    return `NGN ${(amountInKobo / 100).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`;
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleString();
  };

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'success':
        return <Badge variant="default">Success</Badge>;
      case 'pending':
        return <Badge variant="outline">Pending</Badge>;
      case 'failed':
        return <Badge variant="destructive">Failed</Badge>;
      default:
        return <Badge variant="secondary">{status}</Badge>;
    }
  }

  return (
     <Card className="border border-border">
      <CardHeader className="flex flex-row items-center justify-between">
        <div>
          <CardTitle>Transaction History</CardTitle>
          <CardDescription>
            Your most recent transactions.
          </CardDescription>
        </div>
        <Button variant="outline" size="icon" onClick={fetchHistory} disabled={loading}>
          <RefreshCw className={`h-4 w-4 ${loading ? 'animate-spin' : ''}`} />
        </Button>
      </CardHeader>
      <CardContent>
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Customer</TableHead>
              <TableHead>Amount</TableHead>
              <TableHead>Date</TableHead>
              <TableHead>Status</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {loading && transactions?.length === 0 && (
              Array.from({ length: 3 }).map((_, i) => (
                <TableRow key={i}>
                  <TableCell><Skeleton className="h-4 w-32" /></TableCell>
                  <TableCell><Skeleton className="h-4 w-20" /></TableCell>
                  <TableCell><Skeleton className="h-4 w-40" /></TableCell>
                  <TableCell><Skeleton className="h-4 w-16" /></TableCell>
                </TableRow>
              ))
            )}

            {!loading && error && (
              <TableRow>
                <TableCell colSpan={4} className="text-center text-red-600">
                  {error}
                </TableCell>
              </TableRow>
            )}

            {!loading && !error && transactions.length === 0 && (
              <TableRow>
                <TableCell colSpan={4} className="text-center">
                  No transactions found.
                </TableCell>
              </TableRow>
            )}

            {!loading && !error && transactions.map((tx) => (
              <TableRow key={tx.reference}>
                <TableCell>
                    <div className="font-medium">{tx.fullName || 'N/A'}</div>
                    <div className="text-xs text-muted-foreground">{tx.email || tx.customer?.email}</div>
                </TableCell>
                <TableCell>{formatCurrency(tx.amount)}</TableCell>
                <TableCell>{formatDate(tx.timestamp || tx.paid_at || new Date().toISOString())}</TableCell>
                <TableCell>{getStatusBadge(tx.status)}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </CardContent>
    </Card>
  );
}